import Scroll from '@/base/scroll/scroll'
import list from '@/base/list/list'
import loading from '@/base/loading/loading'
import { mapActions, mapGetters } from 'vuex'
import {playlistMixin} from '../../common/js/mixin'
export default {
  mixins: [playlistMixin],
  props: {
    singer: {},
    songs: {}
  },
  data () {
    return {
      imgHeight: 0,
      listHeight: 0
    }
  },
  computed: {
    bgImg () {
      return `background: url('${this.singer.avatar}') no-repeat;
              backgroundSize: 100%`
    },
    ...mapGetters(['playlist', 'currentIndex'])
  },
  components: {
    Scroll,
    list,
    loading
  },
  mounted () {
    this._setHeight()
  },
  methods: {
    ...mapActions(['selectPlay', 'randomPlay']),
    _setHeight () {
      this.imgHeight = this.$refs['bg-img'].clientHeight
      this.listHeight =
        document.documentElement.clientHeight -
        this.$refs['bg-img'].clientHeight
      this.$refs['list'].style.height = `${this.listHeight}px`
    },
    scroll (pos) {},
    select (song, index) {
      let self = this
      this.selectPlay({
        list: self.songs,
        index
      })
    },
    randomPlayAll () {
      this.randomPlay({ list: this.songs })
    },
    handlePlaylist () {
      let bottom
      this.playlist.length > 0 ? bottom = '60px' : bottom = ''
      this.$refs.list.style.paddingBottom = bottom
      this.$nextTick(() => {
        this.$refs.scroll.refresh()
      })
    }
  }
}



